.product-img-box {
	img {
		width: 200px;
		height: 200px;
	}
}

//Барааны дэлгэрэнгүй зураг
.pd-image {
	width: 100px;
	height: 100px;
}
.thumbnail{
	border:0px;
	text-align:center;
	margin-bottom:0px;
	margin-top:15%;
}


.wrapper{
	padding:4%;
	border: 1px solid #fff;
	cursor:pointer;
	background-color:#F2F2F2;
	    height: 7%;
}
h6 a{
	color: #3c3b48;
	font-size: 14px;
	line-height: 18px;
	font-weight: 400;
	font-family: "Raleway", sans-serif;
	src: url(https://fonts.googleapis.com/css?family=Lato|Raleway) format("truetype");
}
h6{
	text-align:center;
}
.product-price{
	font-size: 100%;
	font-weight: 700;
	line-height: 1.3;
	color: inherit;
	font-family: "Lato";
	font-style: normal;
	text-align: center;
}
.product-actions .wrapper .glyphicon:hover{
	color:orange;
}
.product-actions .wrapper .glyphicon{
	color:#bbbfc7;
}

// product title

.producttitle {
	background: url(http://glory.demo1.wpdance.com/supermarket/wp-content/themes/wp_glory/images/media/bg-breadcrumb.jpg);
	background-position: center center!important;
    background-size: cover !important;
    position: relative;
    z-index: 0;
    padding: 77px 0;
}
.titlecolor {
	color:white;
}

// product
.container-fluid {
    background-color: white;
}
.price {
	color:#ed764f;
}
.social {
	float: right;
}